import React, { useEffect, useState } from 'react'
import './style.css'
import * as tick1 from '../../assets/images/Game_Main_Screen/tick 1.png';

import * as menu from '../../assets/Clean Walk/Video/VID_next.png'
import * as arrow from '../../assets/images/Game_Main_Screen/arrow.png';
import { useHistory } from 'react-router-dom';


import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore';
import { userActiveSubLevelDetailsStore } from '../../store/ActiveSubLevelDetailsStore';

import { useActiveScreen } from '../../store/ActiveScreen';
const TaskScreen = (props: any) => {
  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();


  const history = useHistory();
  const [list, setlist] = useState(
    {
      "Level_name": "-",
      "Task_Name": "-",
      "about": '-',
      "Description": "-",
      "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_01_on.png",
      "Location": "-",
      "taskdata": [
        { "number": 1, "Level": "Watch", "status": 'ACTIVE' },
        { "number": 2, "Level": "Activity", "status": 'LOCKED' },
        { "number": 3, "Level": "Quiz", "status": 'LOCKED' },
      ]
    }
  )

  const { setActiveScreen } = useActiveScreen();

  useEffect(() => {
    if (!ActiveLevelDetails) {
      loadActiveleveldetails(localStorage.getItem("ActiveLevel"))
    }
  }, [])
  const openSubLevel = (obj: any) => {
    console.log(obj);
    setActiveScreen("SubLevelScreen")
    loadActiveSubleveldetails(obj.id)
    localStorage.setItem("ActiveSubLevel", obj.id)
  }

  return (
    <div style={{ backgroundColor: "#f1fdfd" }}>
      <div className="tasklist_topbanner">
        <div className="tasklist_banner">

          <div className="tasklist_bannermenu" onClick={() => {
            // history.replace('/')
          }}>
            <img className='tasklist_menuicon rotateimg180' src={menu} />
          </div>

          <div className="tasklist_bannerimg">
            <img className='tasklist_level1' src={String((ActiveLevelDetails) && ActiveLevelDetails.l_icon)} />
          </div>

          <div className="tasklist_bannertext">
            <div className="tasklist_sub-text1">{(ActiveLevelDetails) && ActiveLevelDetails.l_title}</div>
          </div>

          {/* {((list.taskdata[0].status === "DONE") && (list.taskdata[1].status === "DONE") && (list.taskdata[2].status === "DONE")) ?
            <div className="tasklist_reward_icon_container" onClick={() => {
              // history.replace('/RewardScreen/' + params.id)
            }}>
              <img className='tasklist_banner_rewardicon' src={require("../../assets/Clean Walk/Congratulations/medal" + localStorage.getItem("ActiveLevel") + ".png")} />
            </div> : null
          } */}
        </div>
      </div>
      <div className="tasklist_bottommaincontainer">
        <div className="tasklist_about">
          <p className="taskscreen_description">
            {(ActiveLevelDetails) && ActiveLevelDetails.l_body}
          </p>
        </div>
        <div className="tasklist_listitem_container">

          {(ActiveLevelDetails) && ActiveLevelDetails.SubLevelData.map((object: any, index: any) => {

            if (object.sl_types === "VIDEO") {
              return <> <div className={(list.taskdata[0].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => { openSubLevel(object) }}>
                <div className="tasklist_sub_level_container">
                  <div className='tasklist_sub_level_subcontainer1'>
                    <div className="tasklist_listnumber">{index}</div>
                    <div className="tasklist_listtext">{object.sl_title}</div>
                    {(list.taskdata[0].status === "DONE") ? <img className='tasklist_button' src={object.sl_icon} /> : <img className='tasklist_button' src={object.sl_icon} />}
                  </div>
                </div>
              </div></>
            } else if (object.sl_types === "QUIZ") {
              return <><div className={(list.taskdata[1].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => { openSubLevel(object) }}>
                <div className="tasklist_sub_level_container">
                  <div className='tasklist_sub_level_subcontainer2'>
                    <div className="tasklist_listnumber">{index}</div>
                    <div className="tasklist_listtext">{object.sl_title}</div>
                    {(list.taskdata[1].status === "DONE") ? <img className='tasklist_button' src={object.sl_icon} /> : <img className='tasklist_button' src={object.sl_icon} />}
                  </div>

                </div>
              </div></>
            } else if (object.sl_types === "ACTIVITIES") {
              return <><div className={(list.taskdata[2].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => { openSubLevel(object) }}>
                <div className="tasklist_sub_level_container">
                  {/* <img className='tasklist_sub_level1' src={gm1s} /> */}
                  <div className='tasklist_sub_level_subcontainer3'>
                    <div className="tasklist_listnumber">{index}</div>
                    <div className="tasklist_listtext">{object.sl_title}</div>
                    {(list.taskdata[2].status === "DONE") ? <img className='tasklist_button' src={object.sl_icon} /> : <img className='tasklist_button' src={object.sl_icon} />}
                  </div>
                  {/* {(list.taskdata[2].status === "DONE") ? <img className='tasklist_tick' src={tick1} /> : null}
                  {(list.taskdata[2].status === "ACTIVE") ? <img className='tasklist_arrow' src={arrow} /> : null} */}
                </div>
              </div></>
            }
            else if (object.sl_types === "OTHERS") {
              return <><div className={(list.taskdata[2].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => { openSubLevel(object) }}>
                <div className="tasklist_sub_level_container">
                  {/* <img className='tasklist_sub_level1' src={gm1s} /> */}
                  <div className='tasklist_sub_level_subcontainer3'>
                    <div className="tasklist_listnumber">{index}</div>
                    <div className="tasklist_listtext">{object.sl_title}</div>
                    {(list.taskdata[2].status === "DONE") ? <img className='tasklist_button' src={object.sl_icon} /> : <img className='tasklist_button' src={object.sl_icon} />}
                  </div>
                  {(list.taskdata[2].status === "DONE") ? <img className='tasklist_tick' src={tick1} /> : null}
                  {(list.taskdata[2].status === "ACTIVE") ? <img className='tasklist_arrow' src={arrow} /> : null}
                </div>
              </div></>
            }

          })
          }

        </div>

        <div className="bgbottombluebar" />

      </div>
    </div >
  )
}


export default TaskScreen
